<template>
	<div id="creditshop-index">
		<public-header title="积分商城"></public-header>
		<div class="content">
			<van-swipe :autoplay="3000" v-if="adv.length > 0">
				<van-swipe-item v-for="(item, index) in adv" :key="index">
					<router-link tag="div" :to="util.getUrl({path: 'item.link'})">
						<img :src="item.thumb" alt="">
					</router-link>
				</van-swipe-item>
			</van-swipe>
			<div class="menu">
				<a class="border-1px-r">
					<van-icon name='jifen'></van-icon>
					积分
					<template v-if="member.credit1">
						{{member.credit1}}
					</template>
				</a>
				<router-link :to="util.getUrl({path: '/pages/creditshop/list'})"><van-icon name='sortlight'></van-icon>兑换记录</router-link>
			</div>
			<div class="cell-group" v-if="category.length > 0">
				<div class="col" v-for="(item, index) in category" :key="index">
					<router-link tag="div" :to="util.getUrl({path: '/pages/creditshop/goods', query: {category_id: item.id}})">
						<div class="img">
							<img :src="item.thumb" alt="">
						</div>
						<div class="text ellipsis">{{item.name}}</div>
					</router-link>
				</div>
			</div>
			<div class="cell-group border-1px-b" v-if="goods.length > 0">
				<a><van-icon name='gifts'></van-icon>积分实物兑换</a>
			</div>
			<div class="goods-group" v-if="goods.length > 0">
				<router-link :to="util.getUrl({path: '/pages/creditshop/goodsDetail', query: {id: item.id}})" class="goods-item" v-for="(item, index) in goods" :key="index">
					<div>
						<div class="image">
							<img :src="item.thumb">
						</div>
						<div class="detail">
							<div class="name">{{item.title}}</div>
							<div class="price">
								<div class="price-item">
									<span class="num">{{item.use_credit1}}</span>
									<span class="text">积分</span>
									<span class="num" v-if="item.use_credit2 > 0">+¥{{item.use_credit2}}</span>
								</div>
								<div class="exchange">兑换</div>
							</div>
						</div>
					</div>
				</router-link>
			</div>
			<div class="cell-group border-1px-b" v-if="goods_credit2.length">
				<a><van-icon name='gifts'></van-icon>积分兑换余额</a>
			</div>
			<div class="goods-group line" v-if="goods_credit2.length">
				<router-link :to="util.getUrl({path: '/pages/creditshop/goodsDetail', query: {id: item.id}})" class="goods-item-external border-1px-b" v-for="(item,index) in goods_credit2" :key="index">
					<div class="image">
						<img :src="item.thumb">
					</div>
					<div class="detail">
						<div class="name">{{item.credit2}}</div>
						<div class="price">
							<div class="price-item">
								<span class="num">{{item.use_credit1}}</span>
								<span class="text">积分</span>
								<span class="num" v-if="item.use_credit2 > 0">+¥{{item.use_credit2}}</span>
							</div>
							<div class="exchange">兑换</div>
						</div>
					</div>
				</router-link>
			</div>
			<div class="cell-group border-1px-b" v-if="goods_redpacket.length > 0">
				<a><van-icon name='gifts'></van-icon>积分兑换红包</a>
			</div>
			<div class="goods-group line" v-if="goods_redpacket.length > 0">
				<router-link :to="util.getUrl({path: '/pages/creditshop/goodsDetail', query: {id: item.id}})" class="goods-item-external border-1px-b" v-for="(item, index) in goods_redpacket" :key="index">
					<div class="image">
						<img :src="item.thumb">
					</div>
					<div class="detail">
						<div class="name">{{item.redpacket.name}}</div>
						<div class="price">
							<div class="price-item">
								<span class="num">{{item.use_credit1}}</span>
								<span class="text">积分</span>
								<span class="num" v-if="item.use_credit2 > 0">+¥{{item.use_credit2}}</span>
							</div>
							<div class="exchange">兑换</div>
						</div>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				adv: [],
				goods: [],
				goods_credit2: [],
				goods_redpacket: [],
				category: [],
				member: {}
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'creditshop/index'
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return;
					}
					result = result.message;
					this.adv = [...this.adv, ...result.adv];
					this.goods = [...this.goods, ...result.goods];
					this.goods_credit2 = [...this.goods_credit2, ...result.goods_credit2];
					this.goods_redpacket = [...this.goods_redpacket, ...result.goods_redpacket];
					this.category = [...this.category, ...result.category];
					this.member = result.member;
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#creditshop-index .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#creditshop-index .content::-webkit-scrollbar {
		display: none;
	}
	#creditshop-index .content .van-swipe{
		width: 100%;
		height: 180px;
	}
	#creditshop-index .content .van-swipe img{
		width: 100%;
		height: 180px;
		border: none;
	}
	#creditshop-index .content .menu{
		background: #fff;
		overflow: hidden;
	}
	#creditshop-index .content .menu a{
		padding:11px 0;
		margin:0;
		font-size:14px;
		color:#636363;
		text-align:center;
		width:50%;
		display:inline-block;
		line-height: 22px;
		float: left;
		justify-content: space-between;
	}
	#creditshop-index .content .menu a .van-icon{
		font-size: 14px;
		padding-right: 5px;
	}
	#creditshop-index .content .cell-group{
		margin-top:10px;
		background-color:#fff;
		overflow: hidden;
	}
	#creditshop-index .content .cell-group .col{
		width:25%;
		height:auto;
		position:relative;
		margin:11px 0;
		text-align:center;
		float:left;
	}
	#creditshop-index .content .cell-group .col div{
		display: block;
	}
	#creditshop-index .content .cell-group .col diva .img{
		height:48px;
		margin:auto;
		text-align:center;
		line-height:48px;
	}
	#creditshop-index .content .cell-group .col div .img img{
		height:100%;
		width:48px;
		border-radius:48px;
	}
	#creditshop-index .content .cell-group .col div .text{
		font-size:12px;
		line-height:23px;
		padding:5px;
		padding-top:11px;
		max-height: 100%;

	}
	#creditshop-index .content .cell-group a{
		position:relative;
		padding:16px 13px 14px;
		display:flex;
		-webkit-box-align:center;
		align-items:center;
		line-height:19px;
		color: #000;
		font-size: 15px;
	}
	#creditshop-index .content .cell-group a .van-icon{
		font-size: 14px;
		padding-right: 5px;
	}
	#creditshop-index .content .goods-group{
		background:#fff;
		padding:4px;
		overflow:hidden;
	}
	#creditshop-index .content .goods-group .goods-item{
		width:50%;
		float:left;
		border-bottom:0;
		display:block;
	}
	#creditshop-index .content .goods-group .goods-item div{
		padding: 5px;
	}
	#creditshop-index .content .goods-group .goods-item div .image{
		height:191px;
		overflow:hidden;
		margin:0;
		background-position:center;
		background-repeat:no-repeat;
		background-size:cover;
	}
	#creditshop-index .content .goods-group .goods-item div .image img{
		width:100%;
		height:100%;
	}
	#creditshop-index .content .goods-group .goods-item div .detail{
		padding:11px;
		overflow:hidden;
		-webkit-box-flex:1;
		flex:1;
	}
	#creditshop-index .content .goods-group .goods-item div .detail .name{
		height:37px;
		overflow:hidden;
		text-overflow:ellipsis;
		display:-webkit-box;
		-webkit-line-clamp:2;
		-webkit-box-orient:vertical;
		font-size:14px;
		line-height:19px;
		padding: 0;
		color: #000;
	}
	#creditshop-index .content .goods-group .goods-item div .detail .price{
		position:relative;
		-webkit-box-align:center;
		align-items:center;
		font-size:15px;
		margin-top:6px;
		overflow: hidden;
	}
	#creditshop-index .content .goods-group .goods-item div .detail .price .price-item{
		font-size:12px;
		float:left;
		text-overflow:ellipsis;
		white-space:nowrap;
		overflow:hidden;
		width:75%;
		color:#ff6600;
		padding: 0;
		line-height: 26px;
	}
	#creditshop-index .content .goods-group .goods-item div .detail .price .price-item .num{
		font-size:17px;
		font-weight:bold;
		padding-right:5px;
	}
	#creditshop-index .content .goods-group .goods-item div .detail .price .price-item .text{
		color: #999;
	}
	#creditshop-index .content .goods-group .goods-item div .detail .price .exchange{
		float:right;
		background:#fb4a20;
		line-height:22px;
		color:#fff;
		border-radius:5px;
		font-size:13px;
		padding:0 4px;
		margin-top: 4px;
	}
	#creditshop-index .content .goods-group.line{
		padding: 0;
	}
	#creditshop-index .content .goods-group.line .goods-item-external{
		position:relative;
		height:auto;
		padding:17px;
		background-color:#fff;
		overflow:hidden;
		display:flex;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .image{
		width:83px;
		height:83px;
		float:left;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .image img{
		width: 100%;
		height: 100%;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail{
		-webkit-box-flex:1;
		flex:1;
		padding-left:11px;
		color:#ff6600;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail .name{
		height:44px;
		overflow:hidden;
		text-overflow:ellipsis;
		display:-webkit-box;
		-webkit-line-clamp:2;
		-webkit-box-orient:vertical;
		margin-top:6px;
		font-size:15px;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail .price{
		position:relative;
		-webkit-box-align:center;
		align-items:center;
		font-size:15px;
		margin-top:6px;
		overflow: hidden;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail .price .price-item{
		font-size:12px;
		float:left;
		text-overflow:ellipsis;
		white-space:nowrap;
		overflow:hidden;
		width:78%;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail .price .price-item .num {
		font-size:17px;
		font-weight:bold;
		padding-right:5px;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail .price .price-item .text{
		color: #999;
	}
	#creditshop-index .content .goods-group.line .goods-item-external .detail .price .exchange{
		float:right;
		background:#fb4a20;
		line-height:22px;
		color:#fff;
		border-radius:5px;
		font-size:13px;
		padding:0 4px;
	}
</style>


